const MAP = {
  a: '',
  A: '',
  b: '',
  B: '',
  c: '',
  C: '',
  d: '',
  D: '',
  e: '',
  E: '',
  f: '',
  F: '',
  g: '',
  G: '',
  h: '',
  H: '',
  i: '',
  I: '',
  j: '',
  J: '',
  k: '',
  K: '',
  l: '',
  L: '',
  m: '',
  M: '',
  n: '',
  N: '',
  o: '',
  O: '',
  p: '',
  P: '',
  q: '',
  Q: '',
  r: '',
  R: '',
  s: '',
  S: '',
  t: '',
  T: '',
  v: '',
  V: '',
  u: '',
  U: '',
  w: '',
  W: '',
  x: '',
  X: '',
  y: '',
  Y: '',
  z: '',
  Z: '',
};

/**
 * Return a pseudo localized string
 *
 * @param {String} string
 * @returns {String}
 */
function pseudo(string) {
  let pseudoString = '';
  for (let i = 0; i < string.length; ++i) {
    const c = string.charAt(i);
    if (MAP[c]) {
      const cl = c.toLowerCase();
      if (cl === 'a' || cl === 'e' || cl === 'o' || cl === 'u') {
        pseudoString += MAP[c] + MAP[c];
      } else {
        pseudoString += MAP[c];
      }
    } else {
      pseudoString += c;
    }
  }
  return pseudoString;
};

export default pseudo;
